import React from 'react';
import { Grid } from 'antd-mobile';
import {BrowserRouter as Router,withRouter} from 'react-router-dom';


const data = Array.from(new Array(10)).map(() => ({
  icon: 'http://gw.alicdn.com/bao/uploaded/i3/2531738796/TB2CcuPejuhSKJjSspdXXc11XXa_!!2531738796.jpg_500x500q90.jpg_.webp',
}));


const GridExp = (props) => {
    return  <div style={{marginBottom:'50px'}}>
     <Grid data={data}
      columnNum={2}
      hasLine={false}
      square={false}
      itemStyle={{
        backgroundColor:'rgba(0,0,0,.05)',
        overflow:'hidden',
      }}
      renderItem={dataItem => (
        <div style={{
            marginLeft:'5px',
            width:'180px',
            height:'250px',
            backgroundColor:'white',
            borderRadius:'10px',
            overflow:'hidden',
        }}
        onClick={()=>props.history.push('/React_task/task3/detail')}
        >
          <img src={dataItem.icon} style={{ width: '180px', height: '180px'}} alt="" />
          <div style={{ color: '#000', fontSize: '15px'}}>
            <span>
                欧式创意乡村风平盘
                <div style={{color:'#888', fontSize:'13px',marginTop:'15px'}}><span style={{color:'red',fontSize: '15px'}}>¥ 50</span>  15人已购买</div>
            </span>
          </div>
        </div>
      )}
    />
    </div>
};

export default withRouter(GridExp);