import React from 'react';
import {Button,TabBar} from 'antd-mobile';


const Bottom_bar =(props)=>{
    // const { pathname } = props.location;
    console.log(props)
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0,
                backgroundColor:"white"
            } }>
                <div style={{width:'40%',float:'left'}}>
                <TabBar
                
                    unselectedTintColor="#949494"
                    tintColor="grey"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        key="store"
                        icon={ <div className='iconfont icon-dianpu' />}
                        selectedIcon={ <div className='iconfont icon-dianpu1'/>}
                        onPress={ () => {
                            console.log(props)
                        } }
                    ></TabBar.Item>
                    <TabBar.Item
                        title="客服"
                        key="server"
                        icon={<div  className='iconfont icon-wangwang1'/>}
                        selectedIcon={<div className='iconfont icon-wangwang'/>}
                    ></TabBar.Item>
                    <TabBar.Item
                        title="收藏"
                        key="collect"
                        icon={ <div className='iconfont icon-shoucang' />}
                        selectedIcon={ <div className='iconfont icon-collection-b'/>}
                    ></TabBar.Item>
                </TabBar>
                </div>
                <Button style={{
                    width:'30%',
                    height:'40px',
                    lineHeight:'40px',
                    marginTop:'5px',
                    fontSize:'15px',
                    backgroundImage:'linear-gradient(to right, #FFC500, #FF9402',
                    float:'left',
                    borderRadius:'30px 0 0 30px',
                    color:'white'
                }}>加入购物车</Button>
                <Button style={{
                    width:'30%',
                    height:'40px',
                    lineHeight:'40px',
                    marginTop:'5px',
                    fontSize:'15px',
                    backgroundImage:'linear-gradient(to right, #FF7A00, #FE560A',
                    float:'left',
                    borderRadius:'0 30px 30px 0',
                    color:'white'
                }}>立即购买</Button>
            </div>
        );
}

const Detail = (props) => {
    return <div style={{float:'left'}}>
        <Button style={{
            width:'40px',
            height:'40px',
            fontSize:'30px',
            color:'#fff',
            lineHeight:'36px',
            backgroundColor:'grey',
            opacity:'0.5',
            borderRadius:'20px',
            float:'left',
            position:'absolute',
            zIndex:'1000',
            marginLeft:'25px',
            marginTop:'20px',
        }}
        onClick={()=>props.history.push('/React_task/task3/')}
        >&lt;</Button>
        <img src='http://img.alicdn.com/imgextra/i4/661381228/O1CN015Jfsap1KwSw0FcHlq_!!0-item_pic.jpg_760x760Q50s50.jpg'
            style={{
                height:'30%',
                width:'100%'
            }}
        ></img>
        <Bottom_bar />
        
    </div>
}

export default Detail;
