import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom';
import Home from './taobao/Home';
import Cart from './taobao/Cart';
import Order from './taobao/Order';
import My from './taobao/My';
import Search from './taobao/Search';
import Detail from './taobao/Detail';

const Taobao = ()=>{
	return <Router>
		<Switch>
			<Route exact path='/React_task/task3/' component={Home} />
			<Route path='/React_task/task3/cart' component={Cart} />
			<Route path='/React_task/task3/order' component={Order} />
			<Route path='/React_task/task3/my' component={My} />
			<Route path='/React_task/task3/search' component={Search}/>
			<Route path='/React_task/task3/detail' component={Detail}/>
		</Switch>
	</Router>
}
export default Taobao;